    </div> -->

    <div class="table_container" v-show="listShow">
      <el-table :highlight-current-row="true" :max-height="tableHeight" tooltip-effect="light" ref="table1" border :data="TableData.tableData" style="width: 100%" v-loading="loading1" :summary-method="getSummaries" show-summary>
        <el-table-column :show-overflow-tooltip="true" property="company_name" width="150" label="组织名称"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="warehouse_name" width="138" label="仓库名称"></el-table-column>
        <!-- 1274需求 -->
        <el-table-column :show-overflow-tooltip="true" property="allocation_name" width="180" label="货位名称"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="brand_name" width="120" label="品牌名称"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="goods_id" width="202" label="商品编号"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="goods_name" min-width="500" label="商品名称">
          <template slot-scope="scope">
            <pre>{{scope.row.goods_name}}</pre>
