        </el-table-column>
        <!-- 1274需求 -->
        <el-table-column :show-overflow-tooltip="true" property="batch" width="138" label="年周/批次"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="early_num" width="138px" label="期初数量" align="right"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="early_price" width="138px" label="期初金额" align="right">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="income_num" width="138px;" label="入库数量" align="right"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="income_price" width="138px;" label="入库金额" align="right">
