        <el-table-column :show-overflow-tooltip="true" property="sell_num" width="138px;" label="出库数量" align="right"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="sell_price" width="138px;" label="出库金额" align="right">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="end_num" width="138px;" label="期末数量" align="right"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="end_price" width="138px;" label="期末金额" align="right">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="auxiliary_unit_early_num" width="138px" label="辅助单位期初" align="right"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="auxiliary_unit_income_num" width="138px" label="辅助单位入库" align="right"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="auxiliary_unit_sell_num" width="138px" label="辅助单位出库" align="right"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="auxiliary_unit_end_num" width="138px" label="辅助单位结存" align="right"></el-table-column>

      </el-table>
      <div class="table_sum" ref='table_sum'>
        <ul style="overflow:hidden;width:3100px;" v-loading="loadingsum" element-loading-custom-class="loaddings" element-loading-text="总计数据加载中">
          <li style="width:150px;text-align:right"></li>
          <!-- <li style="width:334px;text-align:center;padding-left:6px;">
