</template>
<script>
import {
  getData,
  opthionList,
  getInventoryFinanceCompanyOpen,
  tableResize //明细表去除页面滚动条
} from "common/utils/public_function/commonUtils";
import formatData from "@/utils/public";
export default {
  name: "reportInventorydetrpt",
  data() {
    return {
      authority: {},
      timeValue1: [],
      params: {},
      searchParams: {},
      listShow: false,

      loadingsum: false,
      TableDataSum: {}, // 升级对应接口
      exportType: null,
      warehouseList: {
        options: []
      }, //仓库管理
      TableData: {
        allList: {},
        tableData: []
      },
      departmentList: {
        options: []
      },
      companyList: [],
      goPage: 1, //默认搜索页面
      pageSize: 30,
      per_page: [30, 50, 100, 500],
      loading: false,
      options4: [],
      list: [],
      tableHeight: 600,
      timeValue: [
        new Date().setTime(new Date().getTime() - 3600 * 1000 * 24 * 30),
        new Date()
      ]

      // rules:{
      //   timeValue:[{
      //      required: true,
      //       message: "请选择业务日期",
      //       trigger: "blur"
      //   }],
      //   company_id:[{
      //      required: true,
      //       message: "请选择销售组织",
      //       trigger: "blur"
      //   }]
      // }
    };
  },

  methods: {
    companyChange(val) {
      this.$set(this.params, "warehouse_id", ""); // 初始化仓库
      this.$set(this.params, "goods_name", " "); // 初始化商品名称
      this.$set(this.params, "goods_id", ""); // 商品编号初始化
      this.$set(this.params, "allocation_id", ""); // 初始化货位
      this.$set(this.params, "batch", ""); // 初始化批次
    },
    warehouseChangeId(val) {
      // 仓库 change
      this.$set(this.params, "allocation_id", ""); // 初始化货位
      this.$set(this.params, "goods_name", ""); // 初始化商品名称
      this.$set(this.params, "goods_id", ""); // 商品编号初始化
      this.$set(this.params, "batch", ""); // 初始化年周/批次
    },
    allocationChangeId(val) {
      // 货位 cahnge
      this.$set(this.params, "goods_name", ""); // 初始化商品名称
      this.$set(this.params, "goods_id", ""); // 商品编号初始化
      this.$set(this.params, "batch", ""); // 初始化年周/批次
    },
    change(val) {
      if (val) {
        //选择的日期
        this.params.start_time = this.formData(val[0]);
        this.params.end_time = this.formData(val[1]);
      } else {
        this.params.start_time = this.params.end_time = "";
      }
    },
    onReset() {
      /** 重置时 如需保存默认参数，则： */
      this.$set(this, "params", {
        company_id: Number(localStorage.company_id)
      });
      this.params.start_time = "";
      this.params.end_time = "";
      this.timeValue = [];
      this.timeValue1 = [];
      this.$set(this, "searchParams", {
        params: {}, //1默认带的参数
        TableData: this.TableData, //列表数据
        API: "v2GetReceiveInfoList", //接口名
        vm: this //传递的this
      });
      Object.assign(this.searchParams.params, this.params, {
        page: 1
      });
      // getData(this.searchParams, 2, tableResize.bind(this));
    },
    handleSelect(value) {
      this.$set(this.params, "goods_id", value.goods_id);
      this.$set(this.params, "batch", "");
    },
    exportOrder(scheduleType) {
      // 导出库存收发明细表导出URL
      if (this.listShow == false) {
        this.$message({
          message: "请查询需要导出的数据",
          type: "error"
        });
        return false;
      }
      let s = Object.assign(this.params, {
        export_type: scheduleType
      });
      s = Object.assign({}, s); // 克隆数据避免数据引用
      this.$axMethod.exportReceiveSummaryUrl(s).then(response => {
        if (response.data.code == 200) {
          window.location.href = response.data.data.url;
        }
      });
    },
    getSummaries(param) {
      let _this = this;
      const { columns, data } = param;
      const sums = [];
      columns.forEach((column, index) => {
        if (index === 1) {
          sums[index] = "合计";
          return;
        } else if (index === 0) {
          sums[index] = "";
          return;
        }
        const values = data.map(item => Number(item[column.property]));
        if (!values.every(value => isNaN(value))) {
          sums[index] = values.reduce((prev, curr) => {
            const value = Number(curr);
            if (!isNaN(value)) {
              return prev + curr;
            } else {
              return prev;
            }
          }, 0);
          if (
            ["early_num", "income_num", "sell_num", "end_num"].includes(
              column.property
            )
          ) {
            sums[index] = sums[index];
          } else if (
            ["early_price", "income_price", "sell_price", "end_price"].includes(
              column.property
            )
          ) {
            sums[index] = Number(sums[index])
              .toFixed(2)
              .toString()
              .replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1" + ",");
          } else {
            sums[index] = "";
          }
        } else {
          sums[index] = "";
        }
      });
      return sums;
    },
    onSearch(formName) {
      //查询
      if (this.timeValue1) {
        this.params.auditor_start_time = this.timeValue1[0];
        this.params.auditor_end_time = this.timeValue1[1];
      } else {
        this.params.auditor_start_time = this.params.auditor_end_time = "";
      }
      var vm = this;
      this.$refs[formName].validate(valid => {
        if (valid) {
          if (!this.params.company_id) {
            // this.$message({
            //   message: "请先选择库存组织",
            //   type: "error"
            // });
          } else {
            this.listShow = true;
            this.loadingsum = true;
            this.$set(this, "searchParams", {
              params: {}, //1默认带的参数
              TableData: this.TableData, //列表数据
              API: "v2GetReceiveInfoList", //接口名
              vm: this //传递的this
            });
            // 对params进行克隆
            const cloneParams = Object.assign({}, this.params);
            // 升级对应接口(计算总计)
            this.$axMethod
              .getAllInventoryInfoList(cloneParams)
              .then(response => {
                if (response.data.code == 200) {
                  this.TableDataSum = response.data.data;
                  this.loadingsum = false;
                }
              });
            Object.assign(this.searchParams.params, this.params, {
              page: 1
            });

            getData(this.searchParams, 2, tableResize.bind(this));
          }
        }
      });
    },
    formData(date) {
      var d = new Date(date),
        month = "" + (d.getMonth() + 1),
        day = "" + d.getDate(),
        year = d.getFullYear();

      if (month.length < 2) month = "0" + month;
      if (day.length < 2) day = "0" + day;

      return [year, month, day].join("-");
    },
    handleSizeChange(val) {
      Object.assign(this.searchParams.params, {
        per_page: val,
        page: 1
      });
      getData(this.searchParams);
    },
    handleCurrentChange(val) {
      Object.assign(this.searchParams.params, {
        page: val
      });
      getData(this.searchParams);
    },
    companyFilter(val) {
      let options = {
        type: 1,
        company_name: val
      };
      getInventoryFinanceCompanyOpen(this, options);
    },
    getCompany() {
      let options = {
        type: 1,
        company_name: null
      };
      getInventoryFinanceCompanyOpen(this, options);
    },
    getWarehouse() {
      this.$common.options("getWarehouseListOption", this.warehouseList, {
        company_id: this.params.company_id
      }); //仓库下拉
    }
  },
  mounted() {
    this.authority = JSON.parse(localStorage.getItem("authority"));
    const elTable1 = this.$refs.table1.$el.getElementsByClassName(
      "el-table__body-wrapper"
    )[0];
    const elTable2 = this.$refs.table_sum;
    elTable1.addEventListener("scroll", function() {
      elTable2.scrollLeft = this.scrollLeft;
    });
    //取消单据右侧滚动条
    tableResize.bind(this)();
    window.onresize = tableResize.bind(this);

    this.params.start_time = this.formData(this.timeValue[0]);
    this.params.end_time = this.formData(this.timeValue[1]);
    this.$set(this, "params", {
      company_id: Number(localStorage.company_id),
      timeValue: [this.params.start_time, this.params.end_time],
      start_time: this.formData(this.timeValue[0]),
      end_time: this.formData(this.timeValue[1])
    });
    opthionList(this, localStorage);
  }
};
</script>

